import { faFilePdf, faDiceD6 } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import { Link } from "react-router-dom";
import Rectangle from "../../assets/img/Rectangle.svg";
import "./ContentCard.scss";

function ContentCard({link, content}) {
  return (
    <>
      <Link to={link} className="ContentCard">
        <article >
          <div className={content?.formatoContenido === ".pdf"? "Content" : "ContentBlue"}>
            <FontAwesomeIcon className="Icon" icon={content?.formatoContenido === ".pdf"? faFilePdf : faDiceD6} />
            <img src={Rectangle} alt="" />
          </div>
          <div className="Titulo">
            {content?.nombreContenido}
          </div>
        </article>
      </Link>
    </>
  );
}

export default ContentCard;
