import { Route, Routes } from "react-router-dom";
import "./App.scss";
import AdminLayoud from "./layoud/AdminLayoud/AdminLayoud";
import UserLayoud from "./layoud/UserLayoud/UserLayoud";
import AdminClients from "./pages/AdminClients/AdminClients";
import AdminContent from "./pages/AdminContent/AdminContent";
import AdminInstitucion from "./pages/AdminInstitucion/AdminInstitucion";
import { AdminSumary } from "./pages/AdminSumary/AdminSumary";
import AdminUsers from "./pages/AdminUsers/AdminUsers";
import CodesGenerator from "./pages/CodesGenerator/CodesGenerator";
import Content from "./pages/Content/Content";
import ContentDownloads from "./pages/ContentDownloads/ContentDownloads";
import Home from "./pages/Home/Home";
import LogIn from "./pages/LogIn/LogIn";
import RecordarPass from "./pages/RecordarPass/RecordarPass";
import SignIn from "./pages/SignIn/SignIn";
import Error404 from "./pages/Error404/Error404";
import IframeContainer from "./components/IframeContainer/IframeContainer";

function App() {
  return (
    <div className="App">
      <Routes>
        <Route path="/" element={<UserLayoud />}>
          <Route path="/" element={<Home />} />
          <Route path="/content/:id" element={<Content />} />
        </Route>
        <Route path="/tknovaRoot" element={<AdminLayoud />}>
          <Route path="/tknovaRoot" element={<AdminSumary />} />
          <Route path="/tknovaRoot/clientes" element={<AdminClients />} />
          <Route
            path="/tknovaRoot/Instituciones"
            element={<AdminInstitucion />}
          />
          <Route path="/tknovaRoot/contenido" element={<AdminContent />} />
          <Route
            path="/tknovaRoot/contenido/:id"
            element={<ContentDownloads />}
          />
          <Route path="/tknovaRoot/Usuarios" element={<AdminUsers />} />
          <Route path="/tknovaRoot/Generador" element={<CodesGenerator />} />
        </Route>

        {/* AgroIndustriaProcesosCarnicos */}
        <Route path="/content-ai/489d316d-9485-44a0-a740-69fc9fd35d93" element={<IframeContainer button src={'http://localhost/tknovaCarnicos/'} />} />
        {/* AgroIndustriaProcesoFruver */}
        <Route path="/content-ai/278d316d-9485-44a0-a740-69fc9fd3qwxa" element={<IframeContainer button src={'https://es.wikipedia.org/wiki/Wikipedia:Portada'} />} />
        {/* AgroIndustriaProcesoNutricion */}
        <Route path="/content-ai/68shk16d-9485-44a0-a740-69fc9fd0sdil" element={<IframeContainer button src={'https://es.wikipedia.org/wiki/Wikipedia:Portada'} />} />

        <Route path="/login" element={<LogIn />} />
        <Route path="/signin" element={<SignIn />} />
        <Route path="/recordpass/:id" element={<RecordarPass />} />
        <Route path="*" element={<Error404 />} />
      </Routes>
    </div>
  );
}

export default App;
